import { NextRequest, NextResponse } from 'next/server'
import { z } from 'zod'

const contactSchema = z.object({
  name: z.string().min(2),
  email: z.string().email(),
  message: z.string().min(5),
  consent: z.boolean(),
  _honeypot: z.string().optional(),
})

type CountRecord = { count: number; ts: number }
const RATE_LIMIT_WINDOW = 60 * 1000 // 1 minute
const MAX_PER_WINDOW = 5
const ipMap = new Map<string, CountRecord>()

export async function POST(req: NextRequest) {
  try {
    const ip = req.headers.get('x-forwarded-for') || req.headers.get('x-real-ip') || 'unknown'
    const now = Date.now()
    const record = ipMap.get(ip)
    if (record && now - record.ts < RATE_LIMIT_WINDOW && record.count >= MAX_PER_WINDOW) {
      return NextResponse.json({ error: 'Rate limit exceeded' }, { status: 429 })
    }
    if (!record || now - record.ts >= RATE_LIMIT_WINDOW) {
      ipMap.set(ip, { count: 1, ts: now })
    } else {
      ipMap.set(ip, { count: record.count + 1, ts: record.ts })
    }

    const body = await req.json()
    const parsed = contactSchema.safeParse(body)
    if (!parsed.success) {
      return NextResponse.json({ error: parsed.error.message }, { status: 422 })
    }
    if (parsed.data._honeypot) {
      return NextResponse.json({ error: 'Spam detected' }, { status: 400 })
    }

    // Log to server console to simulate saving
    console.log('New contact submission: ', { ip, data: parsed.data })

    const requestId = crypto.randomUUID?.() || String(Math.random()).slice(2)

    return NextResponse.json({ ok: true, requestId }, { status: 200 })
  } catch (err) {
    console.error(err)
    return NextResponse.json({ error: 'Server error' }, { status: 500 })
  }
}
